.card {
    width: calc(0.8*260px);
    height: calc(0.8*436px);
    cursor: pointer;
    /* z-index: 50; */

}

.card img {
    width: calc(0.8*260px);

}

.card h4 {
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: #FFFFFF;
}

.card p {
    font-family: 'Rubik', sans-serif;
    font-weight: 400;
    font-size: 13px;
    line-height: 16px;
    text-align: justify;
    color: #CCC3C3;
}