.container {
    height: 100vh;
    width: 100vw;
    padding-top: 32px;
    background: linear-gradient(180deg, #0C0505 0%, #3A3535 47.92%, #5E5858 100%);
}

main {
    height: calc(100% - (98px + 132px));
    display: flex;
    flex-direction: column;
    align-items: center;
}

main h3 {
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: #FFFFFF;
    width: 50%;
    padding: 30px 0px 10px;
}

.card-container {
    padding-top: 5px;
    display: flex;
    gap: 27px;
    border-top: 1px solid #6A6363;
}