footer {
    height: 132px;
    padding: 0px 32px;
    background: linear-gradient(180deg, #383535 0%, rgba(133, 130, 130, 0) 100%);
    border: 2px solid rgba(0, 0, 0, 0.21);
    display: flex;
}

.player {
    width: calc(25% - 32px);
}

.player p {
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: #FFFFFF;
    margin-top: 24px;
}

.player span {
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    color: #FFFFFF;
    margin-top: 8px;
}

.controls {
    width: 50%;
    padding-top: 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.controls__buttons {
    width: 328px;
    height: 64px;
    margin-bottom: 8px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.controls__bar {
    width: 642px;
    height: 20px;
    text-align: center;
}