header {
    height: 97.22px;
    border-bottom: 1px solid #6A6363;
    display: flex;
    justify-content: space-between;
    padding: 0px 32px;
}

header div {
    width: 168px;
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    width: 292px;
    height: 64px;
}

.avatar {
    width: 40px;
    height: 40px;
    border: 2px solid #6A6363;
    border-radius: 50%;
}

header span {
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    color: #FFFFFF;
}